<template>
	<div v-if="genres" id="it-genres">
		<vue-headful title="Géneros - Ink&Tales" />
		<section class="uk-section uk-section-muted uk-section-small">
			<div class="uk-container uk-container-large">
				<h1 class="uk-h2 uk-margin-remove">{{'ALL_GENRES' | translate}}</h1>
				<!-- <ul class="uk-breadcrumb uk-margin-small-top uk-flex-center">
					<li><router-link to="/" >{{'HOME' | translate}}</router-link></li>
					<li><span>{{'GENRES' | translate}}</span></li>
				</ul> -->
			</div>
		</section>
		<section class="uk-section">
			<div class="uk-container uk-container-large">
				<div uk-filter="target: .js-filter" class="uk-margin-large-bottom">
					<ul class="uk-subnav uk-subnav-pill">
						<li class="uk-active" uk-filter-control><a href="#">Todos</a></li>
						<li v-for="genre in genres" v-bind:key="genre.id" :uk-filter-control="'.tag-' + genre.id"><a href="#">{{ genre.name }}</a></li>
					</ul>

					<ul class="js-filter uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m uk-child-width-1-5@l uk-grid-small uk-grid-match uk-text-center" uk-grid>
						<li v-for="serie in series" :key="serie.id" :class="classExtraction(serie)">
							<SerieCard :serie="serie" />
						</li>
					</ul>
				</div>
			</div>
		</section>

	</div>
	<Loading v-else/>
</template>

<script>
	import Loading from "../components/Loading.vue";
	import SerieCard from "../components/SerieCard.vue";
	import { GENRES_ALL_SERIES, HOME_ALL_GENRES } from "../constants/graphql";

	export default {
		components: {
			SerieCard,
			Loading
		},
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				series: []
			};
		},
		methods: {
			classExtraction(item) {
				return item.genres.map(genre => 'tag-' + genre.id);
			}
		},
		apollo: {
			series: GENRES_ALL_SERIES,
			genres: HOME_ALL_GENRES
		}
	};
</script>